import { IEvent, ITeamMatch, IVideo, MediaService } from '@aft-core';
import { Router } from '@angular/router';
import { Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { generateFullPrivateModuleRoute as generateScoutingRoutes } from '@scouting/private/private.routes';
import { PRIVATE_MODULE_ROUTES as SCOUTING_ROUTES } from '@scouting/private/private.routes';
import { Table } from 'primeng/table';
import { DataView } from 'primeng/dataview';
import { CustomTranslatePipe } from '@sw-ui-components';
import { TranslateService } from '@ngx-translate/core';
import { Observable, Subject } from 'rxjs';
import { forkJoin } from 'rxjs';


@Component({
  selector: 'aft-scout-player-videos',
  templateUrl: './player-videos.component.html',
  styleUrls: ['./player-videos.component.scss'],
})
export class PlayerVideosComponent implements OnInit {
  @Input() homeTeam: ITeamMatch | undefined;

  @Input() awayTeam: ITeamMatch | undefined;

  @Input() preview: boolean = false;

  @Input() smallPreview: boolean = false;

  @Input() bigPreview: boolean = false;

  @Input() match: boolean = false;

  @Input() biggerBox: boolean = false;

  @Input() events: IEvent[] = [];

  @ViewChild('dv') dv: DataView | undefined;

  currentLanguage: string ='';

  selectEventTypes:any[]=[];
  selectedEventType:any;

  selectOpponentsTeamName:any[]=[];
  selectedOpponentsTeamName:any;

  videoUrl: string = '';
  teamFlagUrl: string = '';
  display: boolean = false;

  constructor(
    private _router: Router,
    private _mediaService: MediaService,
    private _translate: TranslateService,
    private _customTraslatePipe: CustomTranslatePipe
  ) {}

  ngOnInit(): void {
    this.currentLanguage = this._translate.currentLang;
    this.mockSelectEventTyper();
  }

  goToPlayerDetails(playerId: number | undefined): void {
    if (playerId) {
      this._router.navigate(generateScoutingRoutes(SCOUTING_ROUTES.PLAYER, playerId));
    }
  }

  showVideo(event:IEvent) {
    if (event) {
      this._mediaService.getVideoUrl(event.matchId, event.videoTimestamp).subscribe(
        (videoData: IVideo) => {
          this.videoUrl = videoData.urlVideo;
          this.display = true;
        },
        (error: any) => {
          console.error('Error al obtener la url del video: ', error);
        }
      );
    }
  }

  applyFilterGlobal($event:any, stringVal:string) {
    this.dv?.filter(($event.target as HTMLInputElement).value, stringVal);
  }
  applyFilterSelect($event:any, stringVal:string) {
    this.dv?.filter( $event.value, stringVal);
  }

  async mockSelectEventTyper(){
    const types = ['GOAL','SAVE','RED_CARD','YELLOW_CARD','CARD','ASSIST','CONCEDED_GOAL','DEFENSIVE_DUEL','PENALTY_CONCEDED_GOAL','PENALTY_FOUL','PENALTY_GOAL','PENALTY_SAVE','SHOT_AGAINST','PASS','SHOT'];
    let i = 1
    let promises:any = [];
    for await (const e of types) {
      this.translated('EVENTS.TYPE.'+e.toUpperCase()).subscribe( translationType =>{
        this.selectEventTypes.push({label: translationType, value: e});  
        if(e === 'SAVE'){
          this.selectedEventType = this.selectEventTypes[1].value;
        }   
      });  
        
    }
    
  }

  getEventsFilterSelects(){
    let auxEnvenTypes:any = [];
    let auxEnvenOpponentTeamName:any = [];

    this.events.map( e =>{

      //GET [typeLabel]
      if(!auxEnvenTypes.includes(e.typeLabel)){
        this.translated('EVENTS.TYPE.'+e.typeLabel.toUpperCase()).subscribe( translationType =>{
          this.selectEventTypes.push({label: translationType, value: e.typeShortLabel});  
        });
        auxEnvenTypes.push(e.typeLabel);
      }

      //GET [opponentTeamName]
      if(!auxEnvenOpponentTeamName.includes(e.opponentTeamName)){
        this.selectOpponentsTeamName.push({label: e.opponentTeamName, value: e.opponentTeamName});  
        auxEnvenOpponentTeamName.push(e.opponentTeamName);
      }

    })
  }

  translated(text:string): Observable<string> {
    var subject = new Subject<string>();
    this._customTraslatePipe.transform(text.toUpperCase(), this._translate.currentLang).subscribe(r => subject.next(r) );
    return subject.asObservable();
  }
  
}
